/**
 * Tailwind Base Styles
 */
@import "~flatpickr/dist/flatpickr.min.css";
@import "~@fullcalendar/core/main.min.css";
@import "~quill/dist/quill.core.css";
@import "~quill/dist/quill.snow.css";
@tailwind base;
/**
 * Tailwindcss Components
 */
@tailwind components;
/**
 * Custom Components
 */
.alert {
  @apply .flex .px-4 .py-3 .font-medium .leading-5 .rounded-lg .text-sm .mb-4;
}

.alert-success {
  @apply .text-green-800 .bg-green-100;
}

.alert-failure {
  @apply .text-red-800 .bg-red-100;
}

.alert-warning {
  @apply .text-yellow-800 .bg-yellow-100;
}

.avatar {
  @apply .inline-block .h-10 .w-10 .rounded-full;
}

.avatar-xs {
  @apply .w-6 .h-6;
}

.avatar-sm {
  @apply .h-8 .w-8;
}

.avatar-md {
  @apply .h-10 .w-10;
}

.avatar-lg {
  @apply .h-12 .w-12;
}

.avatar-xl {
  @apply .w-14 .h-14;
}

.avatar-group {
  @apply .flex .overflow-hidden;
}
.avatar-group > .avatar {
  @apply .inline-block .text-white .shadow-solid;
}
.avatar-group > .avatar:not(:first-child) {
  @apply .-ml-1 .shadow-solid;
}

.badge {
  @apply .inline-flex .items-center .px-2.5 .py-0.5 .rounded-full .text-xs .font-medium .leading-4 .bg-gray-100 .text-gray-800;
}

.badge-rounded {
  @apply .rounded;
}

.badge-lg {
  @apply .px-3 .py-0.5 .leading-5;
}

.badge-green {
  @apply .bg-green-100 .text-green-800;
}

.badge-yellow {
  @apply .bg-yellow-100 .text-yellow-800;
}

.badge-red {
  @apply .bg-red-100 .text-red-800;
}

.badge-indigo {
  @apply .bg-indigo-100 .text-indigo-800;
}

.btn-group {
  @apply .relative .z-0 .inline-flex .shadow-sm;
}
.btn-group > .btn {
  @apply .relative;
}
.btn-group > .btn:focus {
  @apply .z-10;
}
.btn-group > .btn:not(:last-child) {
  @apply .rounded-r-none;
}
.btn-group > .btn:not(:first-child) {
  @apply .rounded-l-none .-ml-px;
}

.btn {
  @apply .inline-flex .items-center .justify-center .text-center .px-4 .py-2 .border .border-transparent .text-sm .leading-5 .font-medium .rounded-md .transition .ease-in-out .duration-150;
}
.btn:focus {
  @apply .outline-none .shadow-outline-blue;
}
.btn:active {
  @apply .ease-in-out .duration-150;
}
.btn:disabled {
  @apply .cursor-not-allowed .opacity-50;
}

.btn-block {
  @apply .w-full .block;
}

.btn-xs {
  @apply .px-2.5 .py-1.5 .leading-4;
}

.btn-sm {
  @apply .px-3 .py-2 .leading-4;
}

.btn-md {
  @apply .px-4 .py-2 .leading-5;
}

.btn-lg {
  @apply .px-4 .py-2 .leading-6;
}

.btn-xl {
  @apply .px-6 .py-3 .leading-6;
}

.btn-gray {
  @apply .text-gray-700 .border-gray-300;
}

.btn-indigo {
  @apply .text-white .bg-indigo-600;
}
.btn-indigo:focus {
  @apply .border-indigo-700;
}
.btn-indigo:hover {
  @apply .bg-indigo-500;
}
.btn-indigo:active {
  @apply .bg-indigo-700;
}

.btn-red {
  @apply .text-white .bg-red-600;
}
.btn-red:focus {
  @apply .border-red-700;
}
.btn-red:hover {
  @apply .bg-red-500;
}
.btn-red:active {
  @apply .bg-red-700;
}

.btn-white {
  @apply .text-gray-700 .bg-white .border-gray-300;
}
.btn-white:focus {
  @apply .border-blue-300 .outline-none .shadow-outline-blue;
}
.btn-white:hover {
  @apply .text-gray-500;
}
.btn-white:active {
  @apply .text-gray-700;
}

.btn-flat {
  @apply .text-gray-700 .bg-transparent;
}
.btn-flat:focus {
  @apply .outline-none .shadow-none;
}
.btn-flat:hover {
  @apply .text-gray-500;
}
.btn-flat:active {
  @apply .text-gray-700;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.2s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

[v-cloak] {
  display: none !important;
}

.dropdown-menu {
  @apply .w-48 .rounded-md .bg-white .shadow-xs .py-1;
}

.dropdown-item {
  @apply .block .px-4 .py-2 .text-sm .leading-5 .text-gray-700;
}
.dropdown-item:hover {
  @apply .bg-gray-100 .text-gray-900;
}
.dropdown-item:focus {
  @apply .outline-none .bg-gray-100 .text-gray-900;
}

.form-input, .form-select, .form-textarea {
  @apply .block .w-full .rounded-md .shadow-sm .transition .duration-150 .ease-in-out;
}
.form-input:disabled, .form-select:disabled, .form-textarea:disabled {
  @apply .bg-gray-50;
}

@screen sm {
  .form-input, .form-select {
    @apply .text-sm .leading-5;
  }
}
.invalid-feedback {
  @apply .mt-2 .text-sm .text-red-600;
}

.input-hint {
  @apply .mt-2 .text-sm .text-gray-400;
}

.form-label {
  @apply .block .text-sm .font-medium .leading-5 .text-gray-700;
}

.form-control {
  @apply relative rounded-md shadow-sm;
}

.form-group {
  @apply .block .w-full;
}
.form-group > .form-label {
  @apply .mb-1;
}

.form-check {
  @apply .absolute .flex .items-center .h-5;
}

.form-check-label {
  @apply .ml-2 .block .text-sm .leading-5 .text-gray-900;
}

.form-radio, .form-checkbox {
  @apply .transition .duration-150 .ease-in-out .text-indigo-400;
}

/**
 * Tailwindcss Utilities
 */
@tailwind utilities;
/**
 * Vendor
 */
/**
 * Overrides
 */
.draggable-ghost {
  @apply .bg-transparent .border .border-gray-400 .shadow-none .border-dashed .border-2;
}
.draggable-ghost > * {
  @apply .opacity-0;
}

.fc-day-grid-event {
  @apply .px-2 .py-0 .mx-2 .my-1 .cursor-pointer .text-sm .select-none;
}

.fc-unthemed td.fc-today {
  @apply .bg-gray-100;
}

.fc-day-number {
  @apply .text-xs .font-semibold m-2;
}

thead > tr > .fc-day-header {
  @apply .text-xs .uppercase .font-semibold .text-gray-600;
}

.fc-widget-header {
  @apply .border-gray-200;
}

.fc-unthemed td {
  @apply .border-gray-200;
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #6875f5;
  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #6875f5, 0 0 5px #6875f5;
  opacity: 1;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
  -ms-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;
  border: solid 2px transparent;
  border-top-color: #6875f5;
  border-left-color: #6875f5;
  border-radius: 50%;
  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes nprogress-spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}